import React, { useState } from 'react';
import { VStack, Text, Input, Button, HStack, Skeleton, useToast, Heading, Box, Badge, Image, Progress, Icon } from '@chakra-ui/react';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faArrowRight } from '@fortawesome/free-solid-svg-icons';
import { SearchIcon } from '@chakra-ui/icons';

import youtubelogo from '../images/youtubelogo.png';

function Dashboard() {
  const [selectedStyle, setSelectedStyle] = useState(null);
  const [textInput, setTextInput] = useState('');
  const [response, setResponse] = useState('');
  const [loading, setLoading] = useState(false);
  const toast = useToast();

  const items = [
    {
      id: 1,
      name: 'Username',
      description: 'Kommentar / Review här, Hexagons are the bestagons!',
      icon: '../public/youtubelogo.png',
      storage: 60,
    },
    {
      id: 2,
      name: 'Username',
      description: 'Kommentar / Review här, Hexagons are the bestagons!',
      icon: '../public/youtubelogo.png', 
      storage: 30,
    },
  ];

  // Define style prefixes
  const stylePrefixes = {
    Standard: " ",
    Hjälpsam: "Jag vill att du skall svara extra hjälpsamt ",
    Arg: "Jag vill att du skall svara som du är arg ",
    Professionell: "Svara professionellt och kortfattat "
  };

  // Handle style selection
  const handleStyleSelect = (style) => {
    setSelectedStyle(style);
  };

  // Handle form submission
  const handleSubmit = async () => {
    if (!selectedStyle) {
      toast({
        title: "No style selected.",
        description: "Please select a style before submitting.",
        status: "warning",
        duration: 5000,
        isClosable: true,
      });
      return;
    }

    // Prepend the selected style's prefix
    const prefix = stylePrefixes[selectedStyle] || "";
    const prompt = `${prefix} ${textInput}`;
    setLoading(true);

    try {
      const response = await fetch('https://api.openai.com/v1/chat/completions', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer API-Key-HERE`, // Use environment variable for API key
        },
        body: JSON.stringify({
          model: "gpt-4",
          messages: [
            {
              role: "system",
              content: prefix, // Use the prefix here
            },
            {
              role: "user",
              content: prompt,
            }
          ],
          temperature: 0,
          max_tokens: 550,
          top_p: 1,
          frequency_penalty: 0,
          presence_penalty: 0,
        }),
      });

      if (response.ok) {
        const data = await response.json();
        setResponse(data.choices[0]?.message?.content || "No content available"); // Handle possible missing content
      } else {
        const errorData = await response.json();
        toast({
          title: "Submission failed.",
          description: `Status: ${response.status}. Error: ${errorData.error.message}`,
          status: "error",
          duration: 5000,
          isClosable: true,
        });
      }
    } catch (error) {
      toast({
        title: "An error occurred.",
        description: `Error: ${error.message}`,
        status: "error",
        duration: 5000,
        isClosable: true,
      });
    } finally {
      setLoading(false);
    }
  };

  return (


    
    <VStack spacing={4} align="start" p={5} w="70%">
    <Box bg="gray.800" p={4} borderRadius="lg" w="100%">
    <Heading as="h1" size="xl" color="white">Dashboard</Heading><br></br>

      
      <HStack align="start" spacing={3} mb={4}>
        <Badge colorScheme="whiteAlpha" py={1} px={2} borderRadius="md" variant="solid">1D</Badge>
        <Badge py={1} px={2} borderRadius="md" bg="#2C313D" _hover={{ bg: "gray.700", cursor : "Pointer" }} variant="solid">1W</Badge>
        <Badge py={1} px={2} borderRadius="md" bg="#2C313D" _hover={{ bg: "gray.700", cursor : "Pointer" }} variant="solid">1M</Badge>
        <Badge py={1} px={2} borderRadius="md" bg="#2C313D" _hover={{ bg: "gray.700", cursor : "Pointer" }} variant="solid">1Y</Badge>
        <Badge py={1} px={2} borderRadius="md" bg="#2C313D" _hover={{ bg: "gray.700", cursor : "Pointer" }} variant="solid">Auto Generate All</Badge>



      </HStack>

      {items.map(item => (
        <HStack key={item.id} spacing={4} bg="gray.700" p={4} borderRadius="md" mb={4}>
          <Image boxSize="50px" bgcolor="FFFFFF" alt={item.name} borderRadius="md" src={youtubelogo}   />
          <VStack align="start" spacing={1}>


            <HStack>
            {Object.keys(stylePrefixes).map((style) => (
          <Button
            key={style}
            bg={selectedStyle === style ? "gray.600" : "#2C313D"}
            color="gray.200"
            size="xs"
            borderColor="gray.600"
            onClick={() => handleStyleSelect(style)}
            _hover={{ bg: "gray.600" }}

          >
            {style}
           </Button>
             ))}
            </HStack>

            <VStack>
            <Text color="white" fontWeight="bold">{item.name} <Text color="gray.400" fontSize="sm">{item.description}</Text></Text>


          </VStack>
          </VStack>

          <HStack align="end" spacing={1} mt="0" ml="auto">




<box>

<Button
        bg="#2C313D"
        color="gray.200"
        borderColor="gray.600"
        size="sm"
        mr="15"
        _hover={{ bg: "gray.700" }}
        onClick={handleSubmit}
      >
        Generate answer
      </Button>


          <Input
        bg="#2D3748"
        size="md"
        w="400px"
        mr="15px"
        color="gray.200"
        border="1px solid"
        borderColor="gray.600"
        placeholder=""
        _placeholder={{ color: "gray.400" }}
        value={textInput}
        onChange={(e) => setTextInput(e.target.value)}
      />


<Button 
        size="sm"
        bg="#2C313D"
        color="gray.200"
        borderColor="gray.600"
        _hover={{ bg: "gray.700" }}
        p={5}
        mb="5px"
      >
        <Icon as={FontAwesomeIcon} icon={faArrowRight} boxSize={6} /> {/* Centered Icon */}
      </Button>
      </box>

          </HStack>
          

        </HStack>
      ))}
    </Box>





      
    </VStack>

    
  );
}

export default Dashboard;
